@family {
  font-family: 'Open Sans', 'Arial' sans-serif;
}
h1 {
  color: green;
}
body {
  margin: 0;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 0px;
  height: 0px;
  margin: -1px;
  border: 0;
  padding: 0;
  overflow: hidden;
}
.hidden-image {
  display: none;
}
body {
  background-color: lightcoral;
}
ul {
  list-style: none;
  padding: 0;
}
p {
  margin: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
/*  === Разметка верхней части шапки (лого + меню) === */
.page-header__wrapper {
  background: url(../img/back-mobile-1-x-jpg@3x.png) no-repeat, url(../img/iphone-5-in-hand-png.png) no-repeat;
  background-size: cover;
  width: 100%;
  min-height: 462px;
}
.page-header__top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: nowrap;
  align-items: center;
  min-height: 66px;
  background-color: rgba(0, 0, 0, 0.3);
  overflow: hidden;
  position: relative;
}
.page-header__logo {
  padding-left: 20px;
}
.page-header__menu {
  position: relative;
  width: 50px;
  height: 24px;
  background: url('../img/menu-burger-svg.svg');
  background-size: 50px 24px;
  margin-right: 20px;
}
.main-nav {
  top: 67px;
  z-index: 1;
  display: none;
  padding: 0;
  margin: 0;
  list-style: none;
  width: 100%;
}
.nav-list {
  height: 252px;
  width: 100%;
  margin: 0;
  clip: initial;
  position: relative;
  visibility: visible;
}
.nav-list__item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-height: 63px;
  background-color: #283645;
  box-shadow: 0 -1px 0 #000, inset 0 1px 0 rgba(255, 255, 255, 0.15);
}
.nav-list__item:last-child {
  border-bottom: 1px solid #000000;
}
.nav-list__item-link {
  font-family: 'Open Sans', sans-serif;
  font-size: 18px;
  line-height: 65px;
  font-weight: 700;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
}
/*
    /=== Конец разметки верхней части шапки (лого + меню) ===
*/
/* === Разметка средней части шапки === */
.page-header__top-bottom {
  width: 100%;
  min-height: 396px;
  background-size: cover;
  background-repeat: no-repeat;
}
.page-header__heading {
  position: relative;
  font-family: 'Open Sans', sans-serif;
  font-size: 24px;
  font-weight: 300;
  max-width: 225px;
  text-align: center;
  color: #ffffff;
  margin: 0 auto;
  margin-bottom: 34px;
  padding-top: 29px;
}
.page-header__top-hand {
  min-width: 100%;
  height: 286px;
  background: url(../img/iphone-5-in-hand-png.png) no-repeat;
  background-position: -167px 1px;
}
/* === / Конец разметки средней части шапки === */
/* === Разметка нижней части шапки === */
.page-header__bottom {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #283645;
  height: 271px;
}
.page-header__bottom-download {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 90%;
  height: 55px;
  font-family: 'Open Sans', sans-serif;
  font-size: 18px;
  line-height: 18px;
  font-weight: 700;
  color: #ffffff;
  text-decoration: none;
  border: 3px solid #ffffff;
  border-radius: 35px;
  margin-top: 28px;
}
.header-platforms {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
  min-width: 60%;
  margin: 0;
  margin-top: 32px;
}
.page-header__bottom-description {
  font-family: 'Open Sans', sans-serif;
  font-size: 14px;
  line-height: 24px;
  font-weight: 300;
  color: #ffffff;
  width: 235px;
  text-align: center;
  margin-top: 28px;
}
.promo-features__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f2f2f2;
}
.promo-features__top {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  min-height: 174px;
  background-color: #ffffff;
  min-height: 210px;
}
.promo-features__top-heading {
  font-family: 'Open Sans', sans-serif;
  font-size: 24px;
  font-weight: 300;
  text-align: center;
  color: #283645;
  width: 208px;
  margin-top: 27px;
}
.promo-features__top-subheading {
  font-family: 'Open Sans', sans-serif;
  font-size: 24px;
  font-weight: 300;
  text-align: center;
  color: #283645;
  width: 208px;
  margin-top: 22px;
}
.features-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  width: 100%;
  background-color: #f2f2f2;
  padding-top: 66px;
  margin: 0;
}
.features-list::before {
  content: "";
  position: absolute;
  background: url(../img/triangle-white-main.svg);
  background-repeat: no-repeat;
  width: 100%;
  height: 94px;
  top: -35px;
}
.features-list__item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  width: 84%;
  min-height: 100px;
  margin-bottom: 40px;
}
.features-list__item:last-child {
  background: url(../img/iphone-screen-3.png) no-repeat;
  background-position: center 145px;
  padding-bottom: 322px;
  margin-bottom: 0;
}
.features-item__heading {
  font-family: 'Open Sans', sans-serif;
  font-size: 18px;
  line-height: 24px;
  font-weight: 700;
  color: #000000;
}
.features-item__description {
  font-family: 'Open Sans', sans-serif;
  font-size: 14px;
  line-height: 24px;
  font-weight: normal;
  color: #283645;
  width: 95%;
}
.promo-features__image {
  margin-top: 45px;
  border-radius: 22px;
}
/* Конетейнер слайдера */
.promo-feedback__slideshow-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 365px;
  width: 100%;
  position: relative;
  background-color: #ffffff;
}
.promo-feedback__slideshow-container::after {
  content: "";
  position: absolute;
  background: url(../img/triangle-white-main.svg);
  background-repeat: no-repeat;
  width: 100%;
  height: 94px;
  top: 330px;
}
.slideshow-container__slides {
  display: flex;
  flex-direction: column;
  align-items: center;
}
/* Прячем значения по дефолту */
.slideshow-container__slides {
  display: none;
}
/*Заголовки слайдов */
.slides__heading {
  font-family: 'Open Sans', sans-serif;
  font-size: 18px;
  line-height: 30px;
  font-weight: 700;
  text-align: center;
  color: #000000;
  margin-top: 49px;
  margin-bottom: 22px;
}
/* Текст слайдов */
.slides__text {
  font-family: 'Open Sans', sans-serif;
  font-size: 14px;
  line-height: 30px;
  text-align: center;
  color: #283645;
  width: 87%;
  min-height: 180px;
}
/* Индикаторы переключения слайдов */
.promo-feedback__dots {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 42px;
}
.dot {
  cursor: pointer;
  height: 6px;
  width: 6px;
  background-color: #283645;
  opacity: 0.3;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease;
  margin-right: 13px;
}
.dot:last-of-type {
  margin-right: 0px;
}
.dot:hover {
  opacity: 1;
}
.active {
  display: inline-block;
  border: 3px solid #283645;
  background-color: #ffffff;
  opacity: 1;
}
/* Анимация */
.fade {
  -webkit-animation-name: fade;
  -webkit-animation-duration: 1.5s;
  animation-name: fade;
  animation-duration: 1.5s;
}
@-webkit-keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}
@keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}
.promo-prices__wrapper {
  width: 100%;
  min-height: 506px;
  overflow: hidden;
  background-color: #f2f2f2;
  padding-top: 80px;
}
.promo-prices__input {
  display: none;
}
.prices-table {
  width: 840px;
  box-sizing: border-box;
  border: 2px solid #e5e5e5;
  border-collapse: collapse;
  margin-left: 20px;
  margin-right: 20px;
}
.head-cell {
  width: 276px;
  height: 94px;
  background-color: #283645;
  border: 2px solid #ffffff;
}
.head-cell__name {
  font-family: 'Open Sans', sans-serif;
  font-size: 18px;
  line-height: 30px;
  font-weight: 700;
  text-transform: uppercase;
  color: #ffffff;
}
.head-cell__price {
  font-family: 'Open Sans', sans-serif;
  font-size: 24px;
  line-height: 30px;
  font-weight: 300;
  text-transform: uppercase;
  color: #ffffff;
}
.table-row__cell {
  width: 276px;
  height: 59px;
  padding-left: 22px;
  border: 2px solid #e5e5e5;
}
.table-row__cell__name {
  font-family: 'Open Sans', sans-serif;
  font-size: 14px;
  line-height: 30px;
  font-weight: 500;
  text-transform: uppercase;
  color: #283645;
}
.slideshow-container__controls {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 42px;
}
.slideshow-controls__dot {
  cursor: pointer;
  height: 6px;
  width: 6px;
  background-color: #283645;
  opacity: 0.3;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease;
  margin-right: 13px;
}
.slideshow-controls__dot:last-of-type {
  margin-right: 0px;
}
#price-radio-2:checked ~ .slideshow-container .slideshow-controls label[for=price-radio-2] {
  display: inline-block;
  border: 3px solid #283645;
  background-color: #ffffff;
  opacity: 1;
}
#price-radio-1:checked ~ .slideshow-container .slideshow-controls label[for=price-radio-1] {
  display: inline-block;
  border: 3px solid #283645;
  background-color: #ffffff;
  opacity: 1;
}
#price-radio-3:checked ~ .slideshow-container .slideshow-controls label[for=price-radio-3] {
  display: inline-block;
  border: 3px solid #283645;
  background-color: #ffffff;
  opacity: 1;
}
#price-radio-1:checked ~ .slideshow-container .prices-table {
  transition-duration: 0.7s;
}
#price-radio-2:checked ~ .slideshow-container .prices-table {
  transform: translateX(-280px);
  transition-duration: 0.7s;
}
#price-radio-3:checked ~ .slideshow-container .prices-table {
  transform: translateX(-560px);
  transition-duration: 0.7s;
}
